import { BrowserModule } from '@angular/platform-browser';
import { NgModule } from '@angular/core';
import { HttpModule } from '@angular/http';
import { RouterModule } from '@angular/router';
import {NgbModule} from '@ng-bootstrap/ng-bootstrap';

import { AppComponent } from './app.component';
import { HomeComponent } from './home/home.component';
import { ProductsComponent } from './products/products.component';
import { ShoppingCartComponent } from './shopping-cart/shopping-cart.component';
import { CheckOutComponent } from './check-out/check-out.component';
import { OrderSuccessComponent } from './order-success/order-success.component';
import { MyOrdersComponent } from './my-orders/my-orders.component';
import { AdminProductsComponent } from './admin/admin-products/admin-products.component';
import { AdminOrdersComponent } from './admin/admin-orders/admin-orders.component';
import { LoginComponent } from './login/login.component';
import { componentFactoryName } from '@angular/compiler';
import { BsNavbarComponent } from './bs-navbar/bs-navbar.component';
import {SocialLoginModule,AuthServiceConfig,GoogleLoginProvider, AuthService} from 'ng4-social-login';
import { AuthguardService } from './authguard.service';

const config = new AuthServiceConfig([{
  id : GoogleLoginProvider.PROVIDER_ID,
  provider:new GoogleLoginProvider('332534469753-352hespbgrlg6pq520o4hcuuiimnimpd.apps.googleusercontent.com')
}],false)

export function provideConfig(){
  return config;
}

@NgModule({
  declarations: [
    AppComponent,
    HomeComponent,
    ProductsComponent,
    ShoppingCartComponent,
    CheckOutComponent,
    OrderSuccessComponent,
    MyOrdersComponent,
    AdminProductsComponent,
    AdminOrdersComponent,
    LoginComponent,
    BsNavbarComponent
  ],
  imports: [
    BrowserModule,
    HttpModule,
    SocialLoginModule,
    NgbModule.forRoot(),
    RouterModule.forRoot([
      {path : "" ,            component : HomeComponent},
      {path : 'orders',       component : MyOrdersComponent,canActivate:[AuthguardService]},
      {path:'products' ,      component : ProductsComponent},
      {path:"shoppingcart",   component : ShoppingCartComponent},
      {path:"check-out"   ,   component : CheckOutComponent, canActivate:[AuthguardService]},
      {path:"ordersucess",    component : OrderSuccessComponent,canActivate:[AuthguardService]},
      {path:"login",          component : LoginComponent},
      {path: "admin/products",component : AdminProductsComponent,canActivate:[AuthguardService]},
      {path: "admin/orders",  component : AdminOrdersComponent,canActivate:[AuthguardService]},
    


    ])
  ],
  providers: [
    {
      provide:AuthServiceConfig,useFactory:provideConfig
    },
    [AuthService],
    [AuthguardService]
  ],
  bootstrap: [AppComponent]
})
export class AppModule { }



